body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.root {
  width: 1440px;
  height: 2000px;
}

.container {
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: column
}

.quoteContainer {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: space-between;
  width: 70%;
}

.title {
  font-size: 24px;
  font-weight: bold;
  letter-spacing: -0.1px;
  text-align: center;
  color: #000000;
  height: 100px;
  text-align: center;
}

.innerQuoteContainer {
  padding: 20px;
}

.magzinName {
  text-align: right;
}

.mapContainer {
  padding: 50px;
}

* {
  box-sizing: border-box;
}

.root {
  font-family: Helvetica;
  padding: 20px;
  background: #f1f1f1;
}

.chart text {
  fill: #94A7AF;
  font: 17px Helvetica;
  line-height: 17px;
}
